<template>
  <li
    class="current-play-list-item"
    @click="$emit('change-current-play-song', item)"
  >
    <div
      class="palying-animate"
      :class="{ paused: !playing }"
      v-if="currentPlaySong.id == item.id"
    >
      <span></span>
      <span></span>
      <span></span>
    </div>
    <div class="detail">
      <span class="song">{{ item.name }} - </span>

      <span class="artist" v-for="artist in item.song.artists" :key="artist.id">
        {{ artist.name }}
      </span>
    </div>

    <div class="delete">X</div>
  </li>
</template>

<script>
export default {
  props: {
    item: Object,
    currentPlaySong: Object,
    playing: Boolean,
  },
};
</script>

<style lang="less">
li.current-play-list-item {
  display: flex;

  @keyframes shensuo {
    from {
      height: 20%;
    }
    to {
      height: 100%;
    }
  }
  .palying-animate {
    width: 20px;
    height: 20px;
    // background: green;
    display: flex;
    justify-content: space-between;
    align-items: flex-end;

    span {
      width: 20%;
      height: 10%;
      background: green;
      animation: shensuo 0.6s linear infinite alternate;
     &:nth-child(1) {
        animation-delay: 0s;
      }
      &:nth-child(2) {
        animation-delay: -0.2s;
      }
      &:nth-child(3) {
        animation-delay: -0.4s;
      }
    }
    &.paused {
      span {
        animation-play-state: paused;
      }
    }
  }
  .detail {
    flex: 1;
    border-bottom: 1px solid rgb(219, 216, 216);
    padding: 5px;
    .song{
      // padding: 5px;
    }
  .artist{
    color: #999;
    font-size: 14px;
  }
   
  }
  .delete {
      margin-right: 5px;
      color: #999;
    }
}
</style>