<template>
  <section class="head">
    <div class="head-img">
      <div class="head-l">
        <img
          class="head-bottom"
          :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=400x0`"
          alt=""
        />
      </div>
      <div class="head-r">
        <img
      class="head-top"
      :src="`${detail.coverImgUrl}?imageView=1&type=webp&thumbnail=400x0`"
      alt=""/>
      </div>
    </div>
    
    <ul v-if="detail">
      <MusicListItem
        @change-current-play-song="$emit('change-current-play-song', $event)"
        v-for="(item, index) in detail.tracks"
        :key="item.id"
        :item="item"
        :index="index"
      />
    </ul>
    <div class="song">查看更多歌曲，请下载客户端</div>
    <div class="comment">精彩评论</div>
    <ul>
      <CommentCard
        v-for="item in comments"
        :key="item.commentId"
        :item="item"
      />
    </ul>
    <div class="af">查看全部评论</div>
  </section>
</template>

<script>
import MusicListItem from "../components/MusicListItem.vue";
import CommentCard from "../components/CommentCard.vue";
export default {
  components: {
    MusicListItem,
    CommentCard,
  },
  data: function () {
    return {
      detail: null,
      // comments: null,
      comments: null,
    };
  },
  created() {
    //   获取歌单详情
    this.axios
      .get("https://music.kele8.cn/playlist/detail?id=" + this.$route.query.id)
      .then((res) => {
        this.detail = res.data.playlist;
      });

    // 获取歌单评论
    this.axios
      .get("https://music.kele8.cn/comment/playlist", {
        params: {
          id: this.$route.query.id,
        },
      })
      .then((res) => {
        this.comments = res.data.hotComments;
      });
  },
  
};
</script>

<style lang="less" scoped>
.head {
  .head-img {
    height: 200px;
    overflow: hidden;
    background-size: cover;
    background-position: center;
    filter: blur(30px) brightness(0.9);
    transform: scale(1.5);
    .head-l{
      height: 200px;
      .head-bottom{
        width: 100%;
        height: 200px;
      }
    }
  }
  .head-i{
    .head-top{
      position: absolute;
    }
  }
  .song{
    color: #999;
    font-size: 14px;
    text-align: center;
    margin: 5px 0;
  }
  .comment{
    padding: 0 10px;
    height: 23px;
    line-height: 23px;
    color: #666;
    font-size: 12px;
    font-weight: 400;
    background: rgba(0,0,0,.05);
  }
  .af{
    text-align: center;
    margin: 5px 0;
}
}
</style>